body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
.pictures {
  background: #e5e7e9;
}

.jumbotron,
.footer {
  background: #244385;
  text-align: center;
  color: #ffffff;
  box-shadow: 8px 8px 30px 8px rgba(0,0,0,0.1), 0 6px 20px 0 rgba(0,0,0,0.1);
  font-family: 'Righteous', cursive;
}

.jumbotron {
  height: 11rem;
  padding-top: 1rem;
  margin-bottom: 0;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 4rem;
  padding-top: 1rem;
}

.pictures {
  text-align: center;
  padding-top: 4rem;
}

